.neighborhood-overview .tab-header {
    background-color: $purple_light;
    padding: 30px;
    position: relative;
    text-align: center;
    width: 100%;
}
.neighborhood-overview .tab-header:after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -26px;
    border-width: 26px 26px 0 26px;
    border-color: $purple_light transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: " ";
    z-index: 2;
}
.neighborhood-overview .tab-header h2 {
    color: #fff;
    font-size: 32px;
    line-height: 1.3;
    letter-spacing: -0.9px;
    margin: 0 0 8px;
}
.neighborhood-overview .tab-header p {
    font-family: "Open sans";
    color: #fff;
}
@media (min-width: $(tablet)) {
    .neighborhood-overview .tab-header p {
        max-width: 65%;
        margin-left: auto;
        margin-right: auto;
    }
}
@media (min-width: $(desktop)) {
    .neighborhood-overview .tab-header {
        height: $tabHeaderHeight;
        padding: 40px 30px;
        top: 0;
        left: 0;
        right: 0;
        z-index: 2;
    }
    .neighborhood-overview .tab-header:after {
        display: none;
    }
    .neighborhood-overview .tab-header h2 {
        font-size: 40px;
    }
    .neighborhood-overview .tab-header p {
        max-width: initial;
    }
}
.neighborhood-overview .tabs {
