    text-align: center;
}
.neighborhood-overview .tab-links li {
    font-family: "Open sans";
    background-color: $purple_dark;
    text-align: center;
    display: inline-block;
    position: relative;
    padding: 0;
    margin: 0 3px;
}
.neighborhood-overview .tab-links li.active {
    background-color: $blue;
}
.neighborhood-overview .tab-links li.active:before{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    top:100%;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-top: 14px solid $blue;
    left: 50%;
    margin-left: -14px;
    z-index: 1;
}
.neighborhood-overview .tab-links a {
    display: block;
    height: 50px;
    padding: 14px 12px;
    color: #fff;
    font-size: 18px;
    line-height: 1.2;
    letter-spacing: -0.4px;
    text-decoration: none;
}
